.hover-effect {
  width: 100%;
  height: 100%;
  float: left;
  overflow: hidden;
  position: relative;
  text-align: center;
  cursor: default;
}

.hover-effect .hover-overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  opacity: 0;
  filter: alpha(opacity=0);
  background-color: rgba(0,0,0,0.5);
  transition: all 0.4s cubic-bezier(0.88,-0.99, 0, 1.81);
}

.hover-effect img {
  display: block;
  position: relative;
  transition: all 0.4s cubic-bezier(0.88,-0.99, 0, 1.81);
}

.hover-effect h2 {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  position: relative;
  font-size: 17px;
  background: rgba(0,0,0,0.6);
  transform: translatey(-100px);
  transition: all 0.4s cubic-bezier(0.88,-0.99, 0, 1.81);
  padding: 10px;
}

.hover-effect a.hover-info {
  position: relative;
  top: 50%;
  width: 50%;
  text-align: center;
  -webkit-text-decoration: semibold;
          text-decoration: semibold;
  display: inline-block;
  text-transform: uppercase;
  color: #000;
  border: 2px solid #fff;
  background-color: #fff;
  opacity: 0;
  filter: alpha(opacity=0);
  transition: all 0.4s ease;
  margin: 50px 0 0;
  padding: 15px 15px;
}

.hover-effect a.hover-info:hover {
  box-shadow: 0 0 5px #ffff;
}

.hover-effect:hover img {
  transform: scale(1.2);
}

.hover-effect:hover .hover-overlay {
  opacity: 1;
  filter: alpha(opacity=100);
}

.hover-effect:hover h2,.hover-effect:hover a.hover-info {
  opacity: .90;
  filter: alpha(opacity=100);
  transform: translatey(0);
}

.hover-effect:hover a.hover-info {
  transition-delay: .2s;
}

